<template>
  <div class="header">
    <a-row>
      <a-col :offset="1" :xs="10" :sm="4" class="first-col">
        <img
          src="./../assets/image/logo.jpg"
          width="50"
          height="50"
          alt="logo"
        />
        <span class="title">JAKIETWO</span>
      </a-col>
      <a-col :sm="8" :xs="10" style="text-align: center;" class="second-col">
        <a-input
          style="border: 0px solid transparent; outline: none;"
          class="search-input"
          placeholder=""
          @search="onSearch"
        />
      </a-col>
      <a-col :sm="10" :xs="4" class="third-col">
        <a-menu v-model="current" mode="horizontal" class="nav-header">
          <a-menu-item key="myhome" @click="routerTo">
            <a-icon type="html5" />
            
          </a-menu-item>
          <a-menu-item key="mycategory" @click="routerTo">
            <a-icon type="tags" />
            
          </a-menu-item>
          <a-menu-item key="myTime" @click="routerTo">
            <a-icon type="branches" />
            
          </a-menu-item>
          <a-menu-item key="myabout" @click="routerTo">
            <a-icon type="man" />
            
          </a-menu-item>
        </a-menu>
        <a-button
          class="login"
          type="primary"
          v-if="!token"
          @click="showLoginModal"
          ></a-button
        >
        <a-button class="register" v-if="!token" @click="signVisible = true"
          ></a-button
        >
        <span v-if="token" style="margin-left: 10px;">{{ username }}</span>
        <a-button
          class="register"
          v-if="token"
          style="margin-left: 10px;"
          @click="logout"
          ></a-button
        >
      </a-col>
    </a-row>
    <!-- -->
    <a-modal title="" v-model="visible">
      <a-form :form="form" @submit="handleSubmit" v-on:keyup.enter="login">
        <a-form-item
          label="username"
          :label-col="{ span: 5 }"
          :wrapper-col="{ span: 12 }"
        >
          <a-input
            v-decorator="[
              'username',
              {
                rules: [
                  { required: true, message: 'Please input your username!' }
                ]
              }
            ]"
          />
        </a-form-item>
        <a-form-item
          label="password"
          :label-col="{ span: 5 }"
          :wrapper-col="{ span: 12 }"
        >
          <a-input
            type="password"
            v-decorator="[
              'password',
              {
                rules: [
                  { required: true, message: 'Please input your password!' }
                ]
              }
            ]"
          />
        </a-form-item>
      </a-form>
      <template slot="footer" style="padding-right: 50px;text-align: center;">
        <a-button
          key="back"
          @click="visible = false"
          style="width: 120px;height: 40px"
          ></a-button
        >
        <a-button
          key="submit"
          type="primary"
          @click="login"
          style="margin-right: 30%; width: 120px ;height: 40px;"
        >
          
        </a-button>
      </template>
    </a-modal>
    <!-- -->
    <a-modal title="" v-model="signVisible">
      <a-form :form="signForm" v-on:keyup.enter="sign">
        <a-form-item
          label="username"
          :label-col="{ span: 5 }"
          :wrapper-col="{ span: 12 }"
        >
          <a-input
            v-decorator="[
              'username',
              {
                rules: [
                  { required: true, message: 'Please input your username!' }
                ]
              }
            ]"
          />
        </a-form-item>
        <a-form-item
          label="password"
          :label-col="{ span: 5 }"
          :wrapper-col="{ span: 12 }"
        >
          <a-input
            type="password"
            v-decorator="[
              'password',
              {
                rules: [
                  { required: true, message: 'Please input your password!' }
                ]
              }
            ]"
          />
        </a-form-item>
      </a-form>
      <template slot="footer" style="padding-right: 50px;text-align: center;">
        <a-button
          key="back"
          @click="signVisible = false"
          style="width: 120px;height: 40px"
          ></a-button
        >
        <a-button
          key="submit"
          type="primary"
          @click="sign"
          style="margin-right: 30%; width: 120px ;height: 40px;"
        >
          
        </a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
import md5 from "js-md5";
import { mapState } from "vuex";
import { login, register } from "../api/user";

export default {
  name: "myheader",
  components: {},
  data() {
    return {
      current: ["myhome"],
      visible: false,
      signVisible: false,
      formLayout: "horizontal",
      form: this.$form.createForm(this),
      signForm: this.$form.createForm(this)
    };
  },
  computed: {
    ...mapState({
      token: state => state.token,
      username: state => state.username,
      allUsers: state => state.allUsers
    })
  },
  watch: {
    current(newval) {
      // if (newval[0] === "myhome") {
      //   this.$router.push({ name: "articleList" });
      // } else if (newval[0] === "mycategory") {
      //   this.$router.push({ name: "category" });
      // } else if (newval[0] === "myTime") {
      //   this.$router.push({ name: "timeline" });
      // } else if (newval[0] === "myabout") {
      //   this.$router.push({ name: "about" });
      // }
    }
  },
  created() {},
  mounted() {
    // this.current = this.$store.state.currentCmp;
  },
  methods: {
    // 
    showLoginModal() {
      this.visible = true;
    },
    login() {
      this.form.validateFields(async (err, values) => {
        if (!err) {
          values.password = md5(values.password);
          // 
          let response = await login(values);
          if (response.success) {
            this.$notification.success({
              message: ""
            });
            this.visible = false;
            this.$store.commit("saveToken", response.token);
            this.$store.commit("saveUsername", values.username);
            this.allUsers.forEach(user => {
              if (user.username === values.username) {
                this.$store.commit("saveUserId", user.id);
              }
            });
          } else {
            this.$notification.error({
              message: "",
              description: response.message
            });
          }
        }
      });
    },
    // 
    sign() {
      let that = this;
      this.signForm.validateFields(async (err, values) => {
        if (!err) {
          values.password = md5(values.password);
          console.log("values", values);
          // 
          let response = await register(values);
          if (response.success) {
            this.$notification.success({
              message: ""
            });
            that.signVisible = false;
          } else {
            this.$notification.error({
              message: "",
              description: response.message
            });
          }
        }
      });
    },
    /**
     *  
     */
    logout() {
      let that = this;
      this.$confirm({
        title: `${this.username}?`,
        content: "",
        onOk() {
          that.$store.commit("saveToken", "");
          that.$store.commit("saveUsername", "");
          that.$store.commit("saveUserId", "");
        },
        onCancel() {}
      });
    },
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
        }
      });
    },
    handleSelectChange(value) {
      this.form.setFieldsValue({
        note: `Hi, ${value === "male" ? "man" : "lady"}!`
      });
    },
    onSearch() {},
    routerTo(obj) {
      let { key } = obj;
      if (key === "myhome") {
        this.$router.push({ name: "articleList" });
      } else if (key === "mycategory") {
        this.$router.push({ name: "category" });
      } else if (key === "myTime") {
        this.$router.push({ name: "timeline" });
      } else if (key === "myabout") {
        this.$router.push({ name: "about" });
      }
    }
  }
};
</script>

<style scoped lang="stylus">
.header
  position fixed
  z-index 10
  width 90%
  max-width 1900px
  box-shadow 0 0 10px rgba(7, 17, 27, .2)
  .first-col
    display flex
    justify-content center
    align-items center
    text-align center
    line-height 60px
    height 60px

    .title
      font-size 18px
      margin-left 5px

  .second-col, .third-col
    display flex
    line-height 60px
    height 60px
    justify-content center
    align-items center

    .login
      margin-right 10px

    .search-input
      height 40px
      width 450px
      outline none
      border none
      &:focus
        border none
        outline none
    .search-input:focus
      border none
      outline none
  .third-col
    text-align right

    .nav-header
      height 60px

      .login, .register
        position relative
        margin-left 5px
        margin-bottom 12px
</style>
<style>
.ant-menu-item {
  height: 60px;
  line-height: 60px;
}
</style>
